import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

const Promotions = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './promotions')
);
const SecondMenu = ({ match }) => {
  console.log("match..... ",match)
  return (<Suspense fallback={<div className="loading" />}>
    <Switch>
      {/* <Redirect exact from={`${match.url}/`} to={`${match.url}/promotions`} /> */}
      <Route
        path={`${match.url}`}
        render={props => <Promotions {...props} />}
      />
      <Redirect to="/error" />
    </Switch>
  </Suspense>
  )
};
export default SecondMenu;
