import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

const Languages = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './languages')
);
const SecondMenu = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      {/* <Redirect exact from={`${match.url}/`} to={`${match.url}/languages`} /> */}
      <Route
        path={`${match.url}`}
        render={props => <Languages {...props} />}
      />
      <Redirect to="/error" />
    </Switch>
  </Suspense>
);
export default SecondMenu;
