import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

const Categories = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './categories')
);
const CategoryDetail = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './categoryDetail')
);
const SecondMenu = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Redirect exact from={`${match.url}/`} to={`${match.url}/list`} />
      <Route
        path={`${match.url}/list`}
        render={props => <Categories {...props} />}
      />
      <Route
        path={`${match.url}/category_detail/:id`}
        render={props => <CategoryDetail {...props} />}
        isExact
      />
      <Redirect to="/error" />
    </Switch>
  </Suspense>
);
export default SecondMenu;
