import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

const States = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './states')
);
const Cities = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './cities')
);
const Countries = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './countries')
);
const Languages = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './languages')
);
const SecondMenu = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Redirect exact from={`${match.url}/`} to={`${match.url}/countries`} />
      <Route
        path={`${match.url}/states`}
        render={props => <States {...props} />}
      />
      <Route
        path={`${match.url}/cities`}
        render={props => <Cities {...props} />}
      />
      <Route
        path={`${match.url}/countries`}
        render={props => <Countries {...props} />}
      />
      <Route
        path={`${match.url}/languages`}
        render={props => <Languages {...props} />}
      />
      <Redirect to="/error" />
    </Switch>
  </Suspense>
);
export default SecondMenu;
