import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

const Brands = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './brands')
);
const BrandDetail = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './brandDetail')
);
const SecondMenu = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Redirect exact from={`${match.url}/`} to={`${match.url}/list`} />
      <Route
        path={`${match.url}/list`}
        render={props => <Brands {...props} />}
      />
      <Route
        path={`${match.url}/brand_detail/:id`}
        render={props => <BrandDetail {...props} />}
        isExact
      />
      <Redirect to="/error" />
    </Switch>
  </Suspense>
);
export default SecondMenu;
