import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

const Products = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './products')
);
const ProductOptions = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './productoption')
);
const ProductOptionValues = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './productoptionvalue')
);
const ShopDetail = React.lazy(()=>
  import('./productdetail')
)
const SecondMenu = ({ match }) => {
  console.log("match url on products...." ,`${match.url}/productoptions/:optionId/optionvalues`)
  return(

  
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Redirect exact from={`${match.url}/`} to={`${match.url}/products`} />
      <Route
        path={`${match.url}/products`}
        render={props => <Products {...props} />}
      />
      <Route
        path={`${match.url}/productoptions`}
        render={props => <ProductOptions {...props} />}
      />
      <Route
        path={`${match.url}/productoptionvalues/:optionId`}
        render={props => <ProductOptionValues {...props} />}
      />
      <Route
        path={`${match.url}/product-detail/:id`}
        render={props => <ShopDetail {...props} />}
      />
      <Redirect to="/error" />
    </Switch>
  </Suspense>
  )
}
  

export default SecondMenu;
