import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

const Shops = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './shops')
);
const ShopDetail = React.lazy(()=>
  import('./shopdetail')
)
const ShopDaysDetail = React.lazy(()=>
  import('./shopdaysdetail')
)
const SecondMenu = ({ match }) => {
  console.log("match on url....",match)
  return(
    <Suspense fallback={<div className="loading" />}>
      <Switch>
        <Redirect exact from={`${match.url}`} to={`${match.url}/shop-list`} />
        <Route
          path={`${match.url}/shop-list`}
          render={props => <Shops {...props} />}
        />
        <Route
          path={`${match.url}/shop-detail/:id`}
          render={props => <ShopDetail {...props} />}
        />
        <Route
          path={`${match.url}/shop-days-detail/:id`}
          render={props => <ShopDaysDetail {...props} />}
        />
        <Redirect to="/error" />
      </Switch>
    </Suspense>
  )
  
}
export default SecondMenu;
