import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

const Products = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './sellerproducts')
);
const ShopDetail = React.lazy(()=>
  import('./sellerproductdetail')
)
const SecondMenu = ({ match }) => {
  console.log("match on sellerproduct......", match)
  return(
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Redirect exact from={`${match.url}/`} to={`${match.url}/list`} />
      <Route
        path={`${match.url}/list`}
        render={props => <Products {...props} />}
      />
      <Route
        path={`${match.url}/product-detail/:id`}
        render={props => <ShopDetail {...props} />}
      />
      <Redirect to="/error" />
    </Switch>
  </Suspense>
  )
}
  

export default SecondMenu;
