import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

const Logs = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './LogList')
);

const LogDetail = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './LogDetail')
);

const SecondMenu = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Redirect exact from={`${match.url}/`} to={`${match.url}/list`} />
      <Route
        path={`${match.url}/list`}
        render={props => <Logs {...props} />}
      />
      <Route
        path={`${match.url}/log-detail/:id`}
        render={props => <LogDetail {...props} />}
        isExact
      />
      <Redirect to="/error" />
    </Switch>
  </Suspense>
);
export default SecondMenu;
