import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

const Hubs = React.lazy(() =>
  import(/* webpackChunkName: "second" */ './hubs')
);
const HubTimes = React.lazy(()=>
  import('./hubtimings')
);
const SecondMenu = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Redirect exact from={`${match.url}/`} to={`${match.url}/hub-list`} />
      <Route
        path={`${match.url}/hub-list`}
        render={props => <Hubs {...props} />}
      />
      {/* <Redirect to="/error" /> */}
      <Route
        path={`${match.url}/hub-timings/:id`}
        render={props => <HubTimes {...props} />}
      />
    </Switch>
  </Suspense>
);
export default SecondMenu;
